.approximator {
  padding: 20px;

  &__group {
	&:not(:last-child) {
	  margin-bottom: 20px;
	}

	&-header {
	  margin-bottom: 10px;
	}

	&-title {
	  font-size: 1rem;
	  font-weight: bold;
	  margin-top: 0;
	  margin-bottom: 2px;
	}

	&-subtitle {
	  font-size: .875rem;
	  margin-top: 0;
	  margin-bottom: 2px;
	}

	&-buttons {
	  display: flex;
	  flex-direction: column;
	  align-items: flex-start;

	  &-row {
		display: flex;
		align-items: center;

		&:not(:last-child) {
		  margin-bottom: 10px;
		}
	  }
	}

	&-button {
	  padding: 7px 15px;
	  border: 1px solid darken(#518de9, 7%);
	  background: #518de9;
	  border-radius: 3px;
	  color: #fff;
	  cursor: pointer;
	  outline: none;

	  &:hover {
		background: darken(#518de9, 7%);
	  }

	  &:not(:last-child) {
		margin-right: 5px;
	  }
	}

	&-file-loader {
	  border: 1px solid #518de9;
	  margin-right: 5px;
	  padding: 5px;
	}
  }

  &__chart-tooltip {
	&-table {
	  border-collapse: collapse;
	  border: 1px solid #bbb;
	  padding-top: 20px;
	  font-size: .625rem;
	  line-height: 1.2;
	}

	&-th, &-td {
	  padding: 5px;
	  text-align: center;
	  width: 120px;
	  max-width: 120px;

	  white-space: pre-wrap; /* css-3 */
	  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
	  white-space: -pre-wrap; /* Opera 4-6 */
	  white-space: -o-pre-wrap; /* Opera 7 */
	  word-wrap: break-word; /* Internet Explorer 5.5+ */
	}
  }
}
