.table {
  width: 100%;

  &__title {
	font-weight: bold;
	margin-top: 0;
	margin-bottom: 10px;
  }

  &__table {
	border: 1px solid #bbb;
	border-collapse: collapse;

	&-tr {

	}

	&-th, &-td {
	  border: 1px solid #bbb;
	  width: 35px;
	  padding: 5px;
	  font-size: .875rem;

	  &_width {
		@for $i from 1 through 10 {
		  &_#{$i} {
			width: 100% / $i;
		  }
		}
	  }
	}

	&-td {

	}

	&-th, &-td, &-input {
	  &_align {
		&_left {
		  text-align: left;
		}

		&_center {
		  text-align: center;
		}

		&_right {
		  text-align: right;
		}
	  }
	}

	&-input {
	  width: 100%;
	  max-width: 100%;
	  border: none;
	  outline: none;
	}
  }
}
